<template>
  <div class="Board">
    <h2>現任隊長: {{ currentLeaderName }}</h2>
    <h2>戰況:</h2>
    <v-data-table
      :headers="resultsTable.headers"
      :items="resultsTable.items"
      hide-actions
      class="elevation-1"
    >
      <template slot="items" scope="props">
        <td>{{ props.item.round }}</td>
        <td>{{ props.item.numOfSoldier }}</td>
        <td>{{ props.item.requiredFail }}</td>
        <td>{{ props.item.result }}</td>
      </template>
    </v-data-table>
    <h2>剩餘投票次數: {{ 5 - voteFailCounter }}</h2>
    <v-btn @click="gotoTeamSelect" success>請隊長按下繼續</v-btn>
  </div>
</template>

<script>
export default {
  name: 'Board',
  data () {
    return {
    }
  },
  computed: {
    resultsTable () {
      return {
        headers: [
          {text: '回合', value: 'round'},
          {text: '所需士兵', value: 'numOfSoldier'},
          {text: '所需失敗卡數', value: 'requiredFail'},
          {text: '任務結果', value: 'result'}
        ],
        items: this.missions
      }
    },
    currentLeaderName () {
      return this.$store.state.game.players[this.$store.state.game.board.currentLeaderIndex].name
    },
    voteFailCounter () {
      return this.$store.state.game.board.voteFailCounter
    },
    missions () {
      return this.$store.state.game.board.missions
    },
    numOfSuccess () {
      return this.missions.filter((mission) => {
        return mission.result === 'success'
      }).length
    },
    numOfFail () {
      return this.missions.filter((mission) => {
        return mission.result === 'fail'
      }).length
    }
  },
  methods: {
    gotoTeamSelect () {
      if (this.numOfSuccess === 3) {
        // win
        console.log('kill')
        this.$router.push('Kill')
      } else if (this.numOfFail === 3 || this.voteFailCounter === 5) {
        // lose
        console.log('lose')
        this.$router.push('Lose')
      } else {
        this.$router.push('TeamSelect')
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.Board {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
button {
  width: 80%;
}
</style>



