<template>
  <div class="Kill">
    <div class="discussSection" v-show="isDiscussing">
      <h2>請邪惡玩家討論誰是梅林</h2>
      <ul>
        <li v-for="evilPlayer in evilPlayers">{{ evilPlayer.name }} 是 {{ evilPlayer.role }}</li>
      </ul>
      <v-btn @click="gotoKillSection" success>繼續</v-btn>
    </div>
    <div class="killSection" v-show="!isDiscussing && !isKilled">
      <h2>請刺客--{{ assassinName }}選擇 , 殺死梅林</h2>
      <ul>
        <li v-for="goodPlayer in goodPlayers">
          <v-radio :label="goodPlayer.name" v-model="killTarget" :value="goodPlayer.name" dark></v-radio>
        </li>
      </ul>
      <v-btn @click="gotoCheckSection" success>確定</v-btn>
    </div>
    <div class="checkSection" v-show="isKilled">
      <h2>刺客--{{ assassinName }}已殺刺完畢</h2>
      <h2>請將您的設備放在中間</h2>
      <v-btn @click="gotoNextPage" success>繼續</v-btn>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Kill',
  data () {
    return {
      killTarget: '',
      isDiscussing: true,
      isKilled: false
    }
  },
  computed: {
    players () {
      return this.$store.state.game.players
    },
    goodPlayers () {
      return this.players.slice(1, this.players.length).filter((player) => {
        return player.team === 'good'
      })
    },
    evilPlayers () {
      return this.players.slice(1, this.players.length).filter((player) => {
        return player.team === 'evil'
      })
    },
    assassinName () {
      return this.evilPlayers.find((evilPlayer) => {
        return evilPlayer.role === '刺客'
      }).name
    },
    merlinName () {
      return this.goodPlayers.find((goodPlayer) => {
        return goodPlayer.role === '梅林'
      }).name
    },
    isKillCorrect () {
      return this.killTarget === this.merlinName
    }
  },
  methods: {
    gotoKillSection () {
      this.isDiscussing = false
    },
    gotoCheckSection () {
      if (this.killTarget === '') {
        alert('請選擇 , 殺死梅林')
      } else {
        this.isKilled = true
      }
    },
    gotoNextPage () {
      if (this.killTarget === this.merlinName) {
        this.$router.push('Lose')
      } else {
        this.$router.push('Win')
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.discussSection {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  button {
    width: 80%;
  }
}
.killSection {
  h2 {
    text-align: center;
  }
  ul {
    list-style: none;
  }
  button {
    width: 80%;
    margin-left: 10%;
  }
}
.checkSection {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  h2 {
    text-align: center;
  }
  button {
    width: 80%;
    margin-left: 10%;
  }
}
</style>



