<template>
  <div class="Mission">
    <div class="readySection" v-show="!isBattling && !isFinishedBattle">
      <h2>請傳給 {{ currentSoldierName }}</h2>
      <v-btn @click="startMission" success>進行任務</v-btn>
    </div>
    <div class="battleSection" v-show="isBattling">
      <ul>
        <li v-for="choice in battleBtnList">
          <v-btn @click="battle(choice)" :class="{ success: (choice === 'success'), fail: (choice === 'fail') }"
          success>
            <v-icon dark left>{{ choice | choiceToIcon }}</v-icon>
            {{ choice | choiceToChinese }}
          </v-btn>
        </li>
      </ul>
    </div>
    <div class="finishedSection" v-show="!isBattling && isFinishedBattle &&!isChecking">
      <h2>所有玩家已完成任務</h2>
      <h2>請將您的設備放在中間</h2>
        <v-btn @click="checkResult" success>查看任務結果</v-btn>
    </div>
    <div class="checkingSection" v-show="isChecking">
      <h1>{{ isMissionSuccess | isMissionSuccessToChinese }}</h1>
      <h2>共有 {{ successCounter.length }} 人任務成功</h2>
      <h2>共有 {{ failCounter.length }} 人任務失敗</h2>
      <v-btn @click="gotoBoard" success>確定</v-btn>
    </div>
  </div>
</template>

<script>
import _ from 'lodash'
export default {
  name: 'Mission',
  data () {
    return {
      isBattling: false,
      isFinishedBattle: false,
      isChecking: false,
      soldierCounter: 0,
      battleBtnList: ['success', 'success', 'fail', 'fail'],
      successCounter: [],
      failCounter: [],
      isMissionSuccess: false
    }
  },
  computed: {
    players () {
      return this.$store.state.game.players
    },
    selectedSoldiersIndex () {
      return this.$store.state.game.board.selectedSoldiersIndex
    },
    selectedSoldiersName () {
      let names = []
      for (let i = 0; i < this.selectedSoldiersIndex.length; i++) {
        names.push(this.players[this.selectedSoldiersIndex[i]].name)
      }
      return names
    },
    currentSoldierName () {
      return this.selectedSoldiersName[this.soldierCounter]
    },
    missions () {
      return this.$store.state.game.board.missions
    },
    currentMissionIndex () {
      return this.$store.state.game.board.currentMissionIndex
    }
  },
  filters: {
    choiceToChinese (choice) {
      if (choice === 'success') {
        return '成功'
      } else {
        return '失敗'
      }
    },
    choiceToIcon (choice) {
      if (choice === 'success') {
        return 'fa-thumbs-o-up'
      } else {
        return 'fa-thumbs-o-down'
      }
    },
    isMissionSuccessToChinese (isMissionSuccess) {
      if (isMissionSuccess) {
        return '任務成功'
      } else {
        return '任務失敗'
      }
    }
  },
  methods: {
    startMission () {
      this.battleBtnList = _.shuffle(this.battleBtnList)
      this.isBattling = true
    },
    gotoReadySection () {
      this.isBattling = false
    },
    gotoFinishedSection () {
      this.isBattling = false
      this.isFinishedBattle = true
    },
    battle (choice) {
      if (choice === 'success') {
        this.successCounter.push(this.currentSoldierName)
      } else {
        this.failCounter.push(this.currentSoldierName)
      }
      this.soldierCounter += 1
      if (this.soldierCounter === this.selectedSoldiersIndex.length) {
        this.gotoFinishedSection()
      } else {
        this.gotoReadySection()
      }
    },
    checkResult () {
      // !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
      // TODO : NEED calu. the no. of fail requied
      this.isMissionSuccess = (this.failCounter.length < this.missions[this.currentMissionIndex].requiredFail)
      this.isChecking = true
    },
    gotoBoard () {
      if (this.isMissionSuccess) {
        this.$store.commit('updateMissionResult', 'success')
      } else {
        this.$store.commit('updateMissionResult', 'fail')
      }
      this.$store.commit('addCurrentMissionIndex')
      this.$store.commit('setLeader')
      this.$router.push('Board')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.Mission {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.readySection {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.finishedSection, .checkingSection {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  button {
    width: 80%;
  }
}
.battleSection {
  ul {
    list-style: none;
  }
}
.success {
  background-color: #388e3c !important;
}
.fail {
  background-color: #f44336 !important;
}
</style>



