<template>
  <div class="Home">
    <h1>阿瓦隆</h1>
    <img src="../assets/梅林.jpg" alt="梅林">
    <br>
    <v-btn success @click="startNewGame" large>開始新遊戲</v-btn>
    <br>
    <v-btn primary disabled large>繼續上回遊戲</v-btn>
    <br>
    <v-btn info disabled large>說明</v-btn>
  </div>
</template>

<script>
export default {
  name: 'Home',
  data () {
    return {
    }
  },
  methods: {
    startNewGame () {
      this.$router.push('Setup')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.Home {
  height: 100vh;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
img, button {
  width: 70%;
}
img {
  border-radius: 10%;
}
</style>



