<template>
  <div class="TeamSelect">
    <h2>請{{ currentLeaderName }}選擇{{ numOfSoldier }}人出兵: </h2>
    <v-checkbox
      v-for="(player, index) in playersWithName"
      :label="player.name"
      :data="player"
      :key="player.name"
      v-model="selectedSoldiersIndex"
      :value="index + 1"
      dark
    >
    </v-checkbox>
    <v-btn @click="gotoVoteDiscuss" success>確定</v-btn>
  </div>
</template>

<script>
export default {
  name: 'TeamSelect',
  data () {
    return {
      selectedSoldiersIndex: []
    }
  },
  computed: {
    playersWithName () {
      return this.$store.state.game.players.slice(1, this.$store.state.game.players.length)
    },
    currentLeaderName () {
      return this.$store.state.game.players[this.$store.state.game.board.currentLeaderIndex].name
    },
    numOfSoldier () {
      return this.$store.state.game.board.missions[this.$store.state.game.board.currentMissionIndex].numOfSoldier
    }
  },
  methods: {
    gotoVoteDiscuss () {
      if (this.selectedSoldiersIndex.length !== this.numOfSoldier) {
        alert('請選擇' + this.numOfSoldier + '人出兵')
      } else {
        this.$store.commit('setSelectedSoldiersIndex', this.selectedSoldiersIndex)
        this.$router.push('VoteDiscuss')
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.TeamSelect {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
button {
  width: 80%;
}
</style>



