<template>
  <div class="CheckRole">
    <div class="readySection" v-show="!isChecking && !isCheckedAll">
      <h2>請傳給 {{ players[currentCheckingPlayerIndex].name }}</h2>
      <v-btn @click="gotoCheckingSection" success>按下確認您的角色</v-btn>
    </div>
    <div class="checkingSection" v-show="isChecking && !isCheckedAll">
      <h2>您的角色是</h2>
      <img class="currentRoleImgFrame" :src="currentRoleImg" :alt="players[currentCheckingPlayerIndex].role">
      <h2>{{ players[currentCheckingPlayerIndex].role }}</h2>
      <div class="importantInfoSection">
        <h3>重要信息</h3>
        <ul>
          <li class="importantInfoList" v-for="importantInfo in importantInfos">{{ importantInfo }}</li>
        </ul>
      </div>
      <v-btn @click="gotoReadySection" success>按下繼續</v-btn>
    </div>
    <div class="finishedSection" v-show="isCheckedAll">
      <h2>所有角色已確認完畢</h2>
      <h2>請將您的設備放在中間</h2>
      <v-btn @click="gotoBoard" success>開始遊戲</v-btn>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CheckRole',
  data () {
    return {
      currentCheckingPlayerIndex: 1,
      isChecking: false,
      isCheckedAll: false,
      importantInfos: [''],
      testArr: ['a', 'f', 'h']
    }
  },
  computed: {
    players () {
      return this.$store.state.game.players
    },
    currentRoleImg () {
      return './static/img/' + this.players[this.currentCheckingPlayerIndex].role + '.jpg'
    },
    isEvil (player) {
      return (player.team === 'evil')
    }
  },
  methods: {
    gotoCheckingSection () {
      let infoList = []
      if (this.players[this.currentCheckingPlayerIndex].team === 'evil' && this.players[this.currentCheckingPlayerIndex].role !== '奧伯倫') {
        for (let i = 1; i < this.players.length; i++) {
          if (this.players[i].team === 'evil' && this.players[i].role !== '奧伯倫' && this.players[i].role !== this.players[this.currentCheckingPlayerIndex].role) {
            infoList.push(this.players[i].name + ' 和您一樣是壞人')
          }
        }
      } else {
        switch (this.players[this.currentCheckingPlayerIndex].role) {
          case '梅林':
            for (let i = 1; i < this.players.length; i++) {
              if (this.players[i].team === 'evil' && this.players[i].role !== '莫德雷德') {
                infoList.push(this.players[i].name + ' 是壞人')
              }
            }
            break
          case '派西維爾':
            console.log('hi')
            for (let i = 1; i < this.players.length; i++) {
              if (this.players[i].role === '梅林' || this.players[i].role === '莫甘娜') {
                infoList.push(this.players[i].name + ' 可能是 *梅林* 或者 *莫甘娜*')
              }
            }
            break
        }
      }
      this.importantInfos = infoList
      console.log(this.importantInfos)
      console.log(infoList)
      this.isChecking = true
    },
    gotoReadySection () {
      console.log(this.currentCheckingPlayerIndex)
      console.log(this.players.length)
      if (this.currentCheckingPlayerIndex === this.players.length - 1) {
        this.isCheckedAll = true
      } else {
        this.currentCheckingPlayerIndex += 1
      }
      this.isChecking = false
    },
    gotoBoard () {
      console.log('gotoBoard')
      this.$store.commit('setMissionsRules')
      this.$store.commit('setLeader')
      this.$router.push('Board')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.CheckRole {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100vh;
}
.currentRoleImgFrame {
  width: 150px;
  height: 150px;
}
.readySection {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  h2 {
    text-align: center;
  }
}
.checkingSection {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.importantInfoSection {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  li {
    font-size: 120%;
  }
}
.finishedSection {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
</style>



