import Vue from 'vue'
import Vuex from 'vuex'

import _ from 'lodash'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    game: {
      foo: 'hello world',
      players: [{}],
      board: {
        missions: [],
        currentLeaderIndex: 0,
        currentMissionIndex: 0,
        selectedSoldiersIndex: [],
        voteFailCounter: 0
      }
    }
  },
  mutations: {
    setPlayers (state, playersName) {
      console.log('there is setPlayers')
      console.log(playersName)
      let roleList = [
        {role: '梅林', team: 'good'},
        {role: '派西維爾', team: 'good'},
        {role: '忠臣', team: 'good'},
        {role: '莫甘娜', team: 'evil'},
        {role: '刺客', team: 'evil'}
      ]
      let playersTotal = playersName.length - 1
      switch (playersTotal) {
        case 6:
          roleList.push({role: '忠臣', team: 'good'})
          break
        case 7:
          roleList.push(
            {role: '忠臣', team: 'good'},
            {role: '奧伯倫', team: 'evil'}
          )
          break
        case 8:
          roleList.push(
            {role: '忠臣', team: 'good'},
            {role: '忠臣', team: 'good'},
            {role: '爪牙', team: 'evil'}
          )
          break
        case 9:
          roleList.push(
            {role: '忠臣', team: 'good'},
            {role: '忠臣', team: 'good'},
            {role: '忠臣', team: 'good'},
            {role: '莫德雷德', team: 'evil'}
          )
          break
        case 10:
          roleList.push(
            {role: '忠臣', team: 'good'},
            {role: '忠臣', team: 'good'},
            {role: '忠臣', team: 'good'},
            {role: '奧伯倫', team: 'evil'},
            {role: '莫德雷德', team: 'evil'}
          )
          break
      }
      roleList = _.shuffle(roleList)
      console.log(roleList)
      for (let i = 0; i < playersTotal; i++) {
        state.game.players.push({
          name: playersName[i + 1],
          role: roleList[i].role,
          team: roleList[i].team
        })
      }
      console.log(state.game.players)
    },
    setMissionsRules (state) {
      console.log('th')
      switch (state.game.players.length - 1) {
        case 5:
          state.game.board.missions.push(
            {round: 1, numOfSoldier: 2, result: '', requiredFail: 1},
            {round: 2, numOfSoldier: 3, result: '', requiredFail: 1},
            {round: 3, numOfSoldier: 2, result: '', requiredFail: 1},
            {round: 4, numOfSoldier: 3, result: '', requiredFail: 1},
            {round: 5, numOfSoldier: 3, result: '', requiredFail: 1}
          )
          break
        case 6:
          state.game.board.missions.push(
            {round: 1, numOfSoldier: 2, result: '', requiredFail: 1},
            {round: 2, numOfSoldier: 3, result: '', requiredFail: 1},
            {round: 3, numOfSoldier: 4, result: '', requiredFail: 1},
            {round: 4, numOfSoldier: 3, result: '', requiredFail: 1},
            {round: 5, numOfSoldier: 4, result: '', requiredFail: 1}
          )
          break
        case 7:
          state.game.board.missions.push(
            {round: 1, numOfSoldier: 2, result: '', requiredFail: 1},
            {round: 2, numOfSoldier: 3, result: '', requiredFail: 1},
            {round: 3, numOfSoldier: 3, result: '', requiredFail: 1},
            {round: 4, numOfSoldier: 4, result: '', requiredFail: 2},
            {round: 5, numOfSoldier: 4, result: '', requiredFail: 1}
          )
          break
        default:
          state.game.board.missions.push(
            {round: 1, numOfSoldier: 3, result: '', requiredFail: 1},
            {round: 2, numOfSoldier: 4, result: '', requiredFail: 1},
            {round: 3, numOfSoldier: 4, result: '', requiredFail: 1},
            {round: 4, numOfSoldier: 5, result: '', requiredFail: 2},
            {round: 5, numOfSoldier: 5, result: '', requiredFail: 1}
          )
          break
      }
    },
    setLeader (state) {
      console.log('119')
      if (state.game.board.currentLeaderIndex === 0) {
        state.game.board.currentLeaderIndex = Math.floor((Math.random() * (state.game.players.length - 1)) + 1)
      } else {
        if (state.game.board.currentLeaderIndex === state.game.players.length - 1) {
          state.game.board.currentLeaderIndex = 1
        } else {
          state.game.board.currentLeaderIndex += 1
        }
      }
    },
    setSelectedSoldiersIndex (state, a) {
      state.game.board.selectedSoldiersIndex = a.sort()
    },
    addVoteFailCounter (state) {
      state.game.board.voteFailCounter += 1
    },
    initVoteFailCounter (state) {
      state.game.board.voteFailCounter = 0
    },
    updateMissionResult (state, result) {
      state.game.board.missions[state.game.board.currentMissionIndex].result = result
    },
    addCurrentMissionIndex (state) {
      state.game.board.currentMissionIndex += 1
    }
  }
})



