import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Setup from '@/components/Setup'
import CheckRole from '@/components/CheckRole'
import Board from '@/components/Board'
import TeamSelect from '@/components/TeamSelect'
import VoteDiscuss from '@/components/VoteDiscuss'
import Vote from '@/components/Vote'
import Mission from '@/components/Mission'
import Lose from '@/components/Lose'
import Kill from '@/components/Kill'
import Win from '@/components/Win'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/Setup',
      name: 'Setup',
      component: Setup
    },
    {
      path: '/CheckRole',
      name: 'CheckRole',
      component: CheckRole
    },
    {
      path: '/Board',
      name: 'Board',
      component: Board
    },
    {
      path: '/TeamSelect',
      name: 'TeamSelect',
      component: TeamSelect
    },
    {
      path: '/VoteDiscuss',
      name: 'VoteDiscuss',
      component: VoteDiscuss
    },
    {
      path: '/Vote',
      name: 'Vote',
      component: Vote
    },
    {
      path: '/Mission',
      name: 'Mission',
      component: Mission
    },
    {
      path: '/Lose',
      name: 'Lose',
      component: Lose
    },
    {
      path: '/Kill',
      name: 'Kill',
      component: Kill
    },
    {
      path: '/Win',
      name: 'Win',
      component: Win
    }
  ]
})



