<template>
  <div class="Setup">
    <div class="no-use"></div>
    <v-layout row wrap>
      <v-flex xs4 class="numOfPlayersBox">
        <v-subheader class="grey--text text--lighten-1" v-text="'玩家人數: '"></v-subheader>
      </v-flex>
      <v-flex xs8>
        <v-select
          v-bind:items="choiceNumOfPlayers"
          v-model="numOfPlayers"
          label="Select"
          class="input-group--focused"
          dark
          item-value="text"
          @input="cleanPlayersName"
        >
        </v-select>
      </v-flex>
    </v-layout>
    <h2>輸入玩家名稱 (傳給左手邊): </h2>
    <ul>
      <li v-for="(playerInput, index) in numOfPlayers">
        <v-text-field
          name="index"
          label="玩家"
          :value="index+1"
          maxlength="10"
          required
        >
        </v-text-field>
      </li>
    </ul>
    <v-btn @click="finishSetting" large block success>Enter</v-btn>
  </div>
</template>

<script>
export default {
  name: 'Setup',
  data () {
    return {
      numOfPlayers: 5,
      choiceNumOfPlayers: [5, 6, 7, 8, 9, 10],
      playersName: ['']
    }
  },
  computed: {
    foo () {
      return this.$store.state.game.foo
    }
  },
  methods: {
    finishSetting () {
      let ulDomIndex = 6
      for (let i = 0; i < this.numOfPlayers; i++) {
        this.playersName.push(this.$el.childNodes[ulDomIndex].childNodes[i].childNodes[0].childNodes[1].childNodes[0].value)
      }
      console.log(this.playersName)
      console.log(this.foo)
      this.$store.commit('setPlayers', this.playersName)
      this.$router.push('CheckRole')
    },
    cleanPlayersName () {
      console.log('clean')
      this.playersName.length = 0
      this.playersName.push('')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.Setup {
  height: 100%;
}
ul {
  list-style-type: none;
  padding-left: 0px;
}
.numOfPlayersBox {
  display: flex;
  justify-content: center;
  .subheader {
    font-size: 20px;
  }
}
</style>



