<template>
  <div class="Lose">
    <h1>邪惡勝利</h1>
    <ul>
      <li v-for="player in players.slice(1, this.players.length)">{{ player.name }} 是 {{ player.role }}</li>
      <v-btn @click="restartGame" error>重新開始遊戲</v-btn>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'Lose',
  data () {
    return {
    }
  },
  computed: {
    players () {
      return this.$store.state.game.players
    }
  },
  methods: {
    restartGame () {
      this.$router.push('/')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
h1 {
  text-align: center;
  background-color: #f44336;
}
ul {
  list-style: none;
}
li {
  font-size: 120%
}
button {
  width: 80%;
  margin-left: 10%;
}
</style>



