<template>
  <div class="Vote">
    <h2>{{ currentLeaderName }}選擇了</h2>
    <ul>
        <li v-for="name in selectedSoldiersName">{{ name }}</li>
    </ul>
    <div class="readySection" v-show="!isVoting && !isVotedAll">
      <h2>請傳給 {{ currentVoterName }}</h2>
      <v-btn @click="gotoVotingSection" success>按下繼續</v-btn>
    </div>
    <div class="votingSection" v-show="isVoting && !isVotedAll">
      <h2>您是否贊成以上玩家出兵</h2>
      <ul>
        <li v-for="choice in voteBtnList">
          <v-btn @click="vote(choice)" :class="{ agree: (choice === 'agree'), disagree: (choice === 'disagree') }" success>
            <v-icon dark left>{{ choice | choiceToIcon }}</v-icon>
            {{ choice | choiceToChinese }}
          </v-btn>
        </li>
      </ul>
    </div>
    <div class="finishedSection" v-show="isVotedAll">
      <div class="readyCheckBox" v-show="!isCheckResultVisible">
        <h2>所有玩家已投票完畢</h2>
        <h2>請將您的設備放在中間</h2>
        <v-btn @click="checkResult" success>查看投票結果</v-btn>
      </div>
      <div class="checkResultBox" v-show="isCheckResultVisible">
        <h1>{{ isSelectSuccess | isSelectSuccessToChinese }}</h1>
        <ul>
          <li v-for="name in agreeCounter">{{ name }} 投了贊成</li>
        </ul>
        <ul>
          <li v-for="name in disagreeCounter">{{ name }} 投了反對</li>
        </ul>
        <v-btn @click="goNextPage" success>{{ isSelectSuccess | isSelectSuccessToNextPageText }}</v-btn>
      </div>
    </div>
  </div>
</template>

<script>
import _ from 'lodash'
export default {
  name: 'Vote',
  data () {
    return {
      voterCounter: 0,
      isVoting: false,
      isVotedAll: false,
      isChecking: false,
      voteBtnList: ['agree', 'agree', 'disagree', 'disagree'],
      agreeCounter: [],
      disagreeCounter: [],
      isSelectSuccess: false,
      isCheckResultVisible: false
    }
  },
  computed: {
    players () {
      return this.$store.state.game.players
    },
    currentLeaderName () {
      return this.players[this.currentLeaderIndex].name
    },
    currentLeaderIndex () {
      return this.$store.state.game.board.currentLeaderIndex
    },
    selectedSoldiersIndex () {
      return this.$store.state.game.board.selectedSoldiersIndex
    },
    selectedSoldiersName () {
      let names = []
      for (let i = 0; i < this.selectedSoldiersIndex.length; i++) {
        names.push(this.players[this.selectedSoldiersIndex[i]].name)
      }
      return names
    },
    currentVoterIndex () {
      if (this.currentLeaderIndex + this.voterCounter >= this.players.length) {
        return (this.voterCounter - (this.players.length - this.currentLeaderIndex - 1))
      } else {
        return (this.currentLeaderIndex + this.voterCounter)
      }
    },
    currentVoterName () {
      return this.players[this.currentVoterIndex].name
    }
  },
  filters: {
    choiceToChinese (choice) {
      if (choice === 'agree') {
        return '贊成'
      } else {
        return '反對'
      }
    },
    choiceToIcon (choice) {
      if (choice === 'agree') {
        return 'fa-check'
      } else {
        return 'fa-times'
      }
    },
    isSelectSuccessToChinese (isSelectSuccess) {
      if (isSelectSuccess) {
        return '出兵成功'
      } else {
        return '出兵失敗'
      }
    },
    isSelectSuccessToNextPageText (isSelectSuccess) {
      if (isSelectSuccess) {
        return '進行任務'
      } else {
        return '重新選擇出兵'
      }
    }
  },
  methods: {
    gotoVotingSection () {
      this.voteBtnList = _.shuffle(this.voteBtnList)
      this.isVoting = true
    },
    gotoReadySection () {
      this.isVoting = false
    },
    gotoVoteResult () {
      this.isVoting = false
      this.isVotedAll = true
    },
    vote (choice) {
      console.log(choice)
      if (choice === 'agree') {
        this.agreeCounter.push(this.currentVoterName)
      } else {
        this.disagreeCounter.push(this.currentVoterName)
      }
      this.voterCounter += 1
      if (this.currentVoterIndex === this.currentLeaderIndex) {
        this.gotoVoteResult()
      } else {
        this.gotoReadySection()
      }
    },
    checkResult () {
      this.isCheckResultVisible = true
      this.isSelectSuccess = (this.agreeCounter.length > this.disagreeCounter.length)
    },
    goNextPage () {
      if (this.isSelectSuccess) {
        this.$store.commit('initVoteFailCounter')
        console.log('go to "mission start"')
        this.$router.push('Mission')
      } else {
        this.$store.commit('setLeader')
        this.$store.commit('addVoteFailCounter')
        this.$router.push('Board')
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.agree {
  background-color: #388e3c !important;
}
.disagree {
  background-color: #f44336 !important;
}
li {
  font-size: 120%
}
button {
  width: 80%;
}
.votingSection {
  ul {
    list-style: none;
    align-items: center;
    display: flex;
    justify-content: center;
    flex-direction: column;
  }
  li {
    width: 50%;
  }
}
.checkResultBox {
  h1 {
    text-align: center
  }
  button {
    margin-left: 10%
  }
}
</style>



