<template>
  <div class="VoteDiscuss">
    <h2>{{ currentLeaderName }}選擇了</h2>
    <ul>
        <li v-for="name in selectedSoldiersName">{{ name }}</li>
    </ul>
    <h3>請討論是否同意以上玩家出兵</h3>
    <h4>(討論完畢後, 投票過程中請保持安靜)</h4>
    <v-btn @click="gotoVote" success>開始投票</v-btn>
  </div>
</template>

<script>
export default {
  name: 'VoteDiscuss',
  data () {
    return {
    }
  },
  computed: {
    players () {
      return this.$store.state.game.players
    },
    currentLeaderName () {
      return this.$store.state.game.players[this.$store.state.game.board.currentLeaderIndex].name
    },
    selectedSoldiersIndex () {
      return this.$store.state.game.board.selectedSoldiersIndex
    },
    selectedSoldiersName () {
      let names = []
      for (let i = 0; i < this.selectedSoldiersIndex.length; i++) {
        names.push(this.players[this.selectedSoldiersIndex[i]].name)
      }
      return names
    }
  },
  methods: {
    gotoVote () {
      this.$router.push('Vote')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



